<template>
  <div class="upload">
		<input
			ref="input"
			type="file"
			name="image"
			accept="image/*"
			@change="setImage"
		/>
    <vue-cropper ref="cropper" :src="imgSrc" v-show="imgSrc" alt="Source Image"> </vue-cropper>
		<div class="btns">
			<el-row>
				<el-button @click.prevent="showFileChooser" type="info">選擇檔案</el-button>
				<el-button @click.prevent="sendImage" type="primary">確認送出</el-button>
			</el-row>
		</div>
  </div>
</template>

<script>
import VueCropper from "vue-cropperjs";
import "cropperjs/dist/cropper.css";
export default {
  components: {
    VueCropper,
  },
  data() {
    return {
      imgSrc: null,
      data: null,
    };
  },
  methods: {
    setImage(e) {
      const file = e.target.files[0];
      if (file.type.indexOf("image/") === -1) {
        alert("Please select an image file");
        return;
      }
      if (typeof FileReader === "function") {
        const reader = new FileReader();
        reader.onload = (event) => {
          this.imgSrc = event.target.result;
					console.log(this.$refs.cropper)
          this.$refs.cropper.replace(event.target.result);
        };
        reader.readAsDataURL(file);
      } else {
        alert("Sorry, FileReader API not supported");
      }
    },
    showFileChooser() {
      this.$refs.input.click();
    },
    sendImage(){
      this.$emit('uploadDiglog',this.$refs.cropper.getCroppedCanvas().toDataURL())
    }
  },
};
</script>

<style lang="scss">
.upload{
	input[name="image"]{
		opacity: 0;
		position: absolute;
	}
	.btns {
		text-align: center;
		margin: 50px auto 0;
	}
}
</style>


